<template>
  <div class="container">
    <div class="cnt-head">
      <router-link to="/" class="breadcrumb">My HexSchool</router-link>
      <select>
        <option>Chapter 1</option>
        <option>Chapter 2</option>
      </select>
      <select>
        <option>Page 1</option>
        <option>Page 2</option>
        <option>Page 3</option>
        <option>Page 4</option>
      </select>
    </div>
    <div class="cnt-body">
      <figure
        class="comic-img"
        :style="{'background-image': 'url(' + comic[0] + ')'}"
      ></figure>
      <a href="#" class="prev"></a>
      <a href="#" class="next"></a>
    </div>
    <div class="cnt-foot">
      <div class="box">
        <ul>
          <li
            v-for="(item, itemIndex) in 10"
            :key="item"
            :class="{'active': itemIndex === 0}"
          >
            <a
              href="#"
              :style="{'background-image': 'url(' + comic[itemIndex] + ')'}"
            >{{ item }}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import p1 from '@/assets/comic/storyboard-1.png'
import p2 from '@/assets/comic/storyboard-2.png'
import p3 from '@/assets/comic/storyboard-3.png'
import p4 from '@/assets/comic/storyboard-4.png'
import p5 from '@/assets/comic/storyboard-5.png'
import p6 from '@/assets/comic/storyboard-6.png'
import p7 from '@/assets/comic/storyboard-7.png'
import p8 from '@/assets/comic/storyboard-8.png'
import p9 from '@/assets/comic/storyboard-9.png'
import p10 from '@/assets/comic/storyboard-10.png'
export default {
  data () {
    return {
      comic: [
        p1,
        p2,
        p3,
        p4,
        p5,
        p6,
        p7,
        p8,
        p9,
        p10
      ]
    }
  }
}
</script>

<style lang="scss">
.cnt-head{
  display: flex;
  flex-wrap: wrap;
  >*{
    flex: 0 0 auto;
    justify-content: center;
    align-items: center;
    margin-right: .3em !important;
  }
  a.breadcrumb{
    display: flex;
    background: none;
    padding: 0;
    margin: 0;
    color: black;
    font-weight: bold;
    &:after{
      content: '';
      display: inline-block;
      width: 0;
      height: 0;
      border-color: transparent transparent transparent black;
      border-style: solid;
      border-width: .3em 0 .3em .6em;
      position: relative;
      top: -.1em;
      margin-left: .3em;
    }
  }
  select{
    color:black;
    font-weight: bold;
    padding: .3em .5em;
    border: 1.5px solid black;
  }
}
.cnt-body{
  position: relative;
  display: flex;
  figure.comic-img{
    display: block;
    width:100%;
    @include bkd(sm){
      width: 90%;
      margin-left: 5%;
    }
    height:0;
    padding-bottom: 160%;
    background-position: center;
    background-size:contain;
    background-repeat: no-repeat
  }
  a{
    position: absolute;
    display: block;
    width: 1.5em;
    height: 2em;
    top: 50%;
    margin-top: -1em;
    $arrow-w: 1em;
    $arrow-h: 1.2em;
    &.next{
      right: -1.5em;
      @include bkd(sm){
        right: 0;
      }
      width: 0;
      height: 0;
      border-style: solid;
      border-width: $arrow-w 0 $arrow-w $arrow-h;
      border-color: transparent transparent transparent black;
    }
    &.prev{
      left: -1.5em;
      @include bkd(sm){
        left: 0;
      }
      width: 0;
      height: 0;
      border-style: solid;
      border-width: $arrow-w $arrow-h $arrow-w 0;
      border-color: transparent black transparent transparent;
    }
  }
}
.cnt-foot{
  display: block;
  width: 100%;
  height: auto;
  ul{
    display: flex;
    width: 100%;
    height: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 1.25em;
    li{
      width: 100%;
      margin-right: 1.25rem;
      @include opacity(.75);
      &.active{
        @include opacity(1);
        a{
          border-color: black;
        }
      }
      a{
        display: block;
        width: 100%;
        min-width: 120px;
        height: 0;
        padding-bottom: 140%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-color: white;
        text-indent: -999em;
        border: 1.25px solid transparent;
      }
    }
  }
}
</style>



