<template>
  <div class="container">
     <div class="cnt-head row">
        <div class="col-12 col-sm-6 cover">
          <figure class="cover-img"
            :style="{'background': 'url(' + cover + ')'}"
          ></figure>
        </div>
        <div class="col-12 col-sm-6 infomations">
          <span class="title">MY HEXSCHOOL</span>
          <span class="info" data-title="Generes">Fusce/vehiculaa/dolor</span>
          <span class="info" data-title="Author">Namae Shiranai</span>
          <span class="info" data-title="Status">Ongoing</span>
          <div class="info rate" data-title="Rate" data-rate="4">
            <i></i>
            <i></i>
            <i></i>
            <i></i>
            <i></i>
          </div>
          <div class="desc">
            If your banker breaks, you snap; if your apothecary by mistake sends you poison in your pills, you die.Therefore, I say, I saw that this situation of mine was the precise situation of every mortal that has this Siamese connexion with a plurality of other mortals.
          </div>
        </div>
     </div>
     <div class="cnt-body row">
       <div class="col-12">
         <div class="title">All Chapters</div>
         <div class="box">
           <ul>
             <li><router-link to="/comic">Chapter 1 : The F2E Chanllenge Start!</router-link></li>
             <li class="new"><router-link to="/comic">Chapter 2 : Todo List is Going Crazy!</router-link></li>
           </ul>
         </div>
       </div>
     </div>
  </div>
</template>

<script>
import cover from '@/assets/comic/cover.png'

export default {
  data () {
    return {
      cover
    }
  }
}
</script>

<style lang="scss">
.cnt-head{
  .cover{
    border: .5rem solid black;
    @include bku(sm){
      padding: 0;
    }
    figure.cover-img{
      display: block;
      width: 100%;
      height: 100%;
      min-height: 380px;
      background-size: contain !important;
      background-position:center !important;
      background-repeat: no-repeat !important;
      background-color: white !important;
    }
  }
  .infomations{
    @include bku(sm){
      padding-left: 0;
    }
    *{
      display: block;
      margin-left: 1.25rem;
      margin-bottom: .3em;
    }
    .title{
      display: block;
      background: black;
      color: white;
      font-size: 1.5em;
      font-weight: bold;
      padding: .3em .5em;
      margin-left: 0;
      margin-bottom: .5em;
    }
    .info[data-title]{
      padding-left: 5em;
      &:before{
        content: attr(data-title);
        display: inline-block;
        font-weight: bold;
        float: left;
        margin-left: -5em;
      }
    }
    .desc{
      &:before{
        content: 'Summary';
        font-weight: bold;
        display: block;
      }
    }
    .rate{
      i{
        margin: 0;
        padding: 0;
        display: inline-block;
        width: 1em;
        height: 1em;
        background-image: url(data:image/svg+xml;base64,);
        background-size: contain;
        &:nth-child(-n + 4){
          background-image: url(data:image/svg+xml;base64,);
        }
      }
    }
  }
}
.cnt-body{
  margin-top: 2.5em;
  .title{
    display: inline-block;
    background: black;
    color: white;
    font-size: 1.3em;
    padding: .5em 1em;
  }
  .box{
    border: .5em solid black;
    ul{
      li{
        padding: .5em 1.25em;
        &:hover{
          background:black;
          a{
            color: white;
          }
        }
        &.new{
          &:after{
            content: 'NEW';
            font-style: italic;
            background-color: default(color, uno);
            display: inline-block;
            padding: .3em .8em;
            line-height: 1em;
            font-weight: bold;
            margin-left: .35em;
            font-size: .8em;
          }
        }
        a{
          color: black;
          font-weight: bold;
        }
      }
    }
  }
}
</style>



