import Vue from 'vue'
import Router from 'vue-router'

import appHome from '@/views/appHome'
import appComic from '@/views/appComic'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: appHome
    },
    {
      path: '/comic',
      component: appComic
    }
  ]
})



